@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  width: 100%;
  display: block;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --red: 0 100% 74%;
  --green: 154 59% 51%;
  --blue: 248 32% 51%;
  --dark-blue:249 10% 26%;
  --gray-blue:246 25% 77%;
  --white: 0 0% 100%;
  --black: 0 0% 8%;
  --selection-bg: 277 100% 50%;
}

/**
 * ### Font family 
 */
/**
 * ### Size variables
 */
/**
 * ### Font weight
 */
::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--white));
}

html {
  font-size: 100%;
  font-family: "Poppins", sans-serif;
}

body {
  background-color: hsl(var(--white));
  font-size: 1rem;
}

p:not([class]) {
  max-width: 45ch;
}

/**
 * Colors
 */
.bg-white {
  background-color: hsl(var(--white));
}

.clr-white {
  color: hsl(var(--white));
}

.bg-black {
  background-color: hsl(var(--black));
}

/**
 * Position
 */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/**
 * ### With container
 */
.container {
  --max-width: 1440px;
  --container-padding: 1rem;
  width: min(var(--max-width), 100% - var(--container-padding) * 0);
  margin-inline: auto;
}

/**
 * ### Flex 
 */
.flex {
  display: flex;
}

.flex-dir-col {
  flex-direction: column;
}

.justify-cont-betw {
  justify-content: space-between;
}

/**
 * ### Grid 
 */
.grid-columns {
  display: grid;
  align-content: space-around;
}
@media (min-width: 52.5em) {
  .grid-columns {
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

/**
 * ### Gap 
 */
.gap-1 {
  gap: 1rem;
}

.gap-2 {
  gap: 2rem;
}

.gap-3 {
  gap: 3rem;
}

/**
 * Float
 */
.float-l {
  float: left;
}

.float-r {
  float: right;
}

/**
 * ### Align items center 
 */
.align-itm-center {
  align-items: center;
}

/**
 * ### Justify content center , start
 */
.justify-cont-center {
  justify-content: center;
}

.justify-cont-start {
  justify-content: start;
}

/**
 * ### Justify items center , start
 */
.justify-item-center {
  justify-items: center;
}

.justify-items-start {
  justify-items: start;
}

/**
 * ### Text  center 
 */
.text-align-center {
  text-align: center;
}

/**
 * Flow -Creating an upper edge for all descendants except the first.
 * inline style --> style="--flow-spacer: 1.5rem"
 */
:where(.flow :not(:first-child)) {
  margin-top: var(--flow-spacer, 3em);
}

/**
 * ### Margin Padding position
 */
.margin-bottom-500 {
  margin-block-end: 1.5rem;
}

/**
 * Padding top and bottom
 */
.padding-block-400 {
  padding-block: 1rem;
}

.padding-block-500 {
  padding-block: 1.5rem;
}

.padding-block-600 {
  padding-block: 2rem;
}

.padding-block-700 {
  padding-block: 3rem;
}

.padding-block-900 {
  padding-block: 5rem;
}

.padding-lr-400 {
  padding-inline: 1rem;
}

.padding-lr-600 {
  padding-inline: 2rem;
}

.padding-lr-800 {
  padding-inline: 4rem;
}

/**
 * ### Font Size
 */
.fs-400 {
  font-size: 1rem;
}

.fs-600 {
  font-size: 2rem;
}

.fs-700 {
  font-size: 3rem;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap; /* added line */
  border: 0;
}

.btn {
  display: inline-flex;
  border: 0;
  padding: 1rem 1.375rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

body {
  width: 100vw;
  height: auto;
  display: flex;
  background-image: url("/images/bg-intro-mobile.png");
  background-color: hsl(var(--red));
}
@media (min-width: 52.5em) {
  body {
    background-image: url("/images/bg-intro-desktop.png");
  }
}

section {
  width: 100%;
  height: 100vh;
}

.box {
  padding: 2.5rem;
  text-align: center;
}
@media (min-width: 52.5em) {
  .box {
    text-align: left;
    padding: 5rem;
    gap: 20px;
  }
}

.col-1,
.trial {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: hsl(var(--white));
}
@media (min-width: 52.5em) {
  .col-1,
.trial {
    align-items: flex-start;
  }
}

h1 {
  font-size: clamp(1.75rem, 1.375rem + 4vw, 3.125rem);
  font-weight: 700;
}

.trial {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  background-color: hsl(var(--blue));
  border-radius: 0.5rem;
  padding: 1.25rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px, rgba(0, 0, 0, 0.2) 0px 7px 13px -3px, rgba(0, 0, 0, 0.3) 0px -3px 0px inset;
}
.trial p {
  font-size: 0.9375rem;
  font-weight: 200;
}
.trial b {
  font-size: 0.9375rem;
  font-weight: 700;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
  background-color: hsl(var(--white));
  border-radius: 0.5rem;
  padding: 1.25rem;
}
@media (min-width: 52.5em) {
  form {
    padding: 2.5rem;
  }
}

.input-wrapp {
  position: relative;
}

input[type=text].card-input,
input[type=email].card-input,
input[type=password].card-input {
  --mywidth: 100%;
  width: var(--mywidth);
  padding: 0.9375rem;
  display: inline-block;
  border: 1px solid hsl(249deg, 10%, 26%);
  border-radius: 4px;
  box-sizing: border-box;
  outline: 0;
}
input[type=text].card-input:focus,
input[type=email].card-input:focus,
input[type=password].card-input:focus {
  border: 1px solid hsl(246deg, 25%, 77%);
  color: hsl(246deg, 25%, 77%);
}

input[type=text].card-input.is-invalid,
input[type=email].card-input.is-invalid,
input[type=password].card-input.is-invalid {
  border: rem(2) solid hsl(0deg, 100%, 74%);
  color: hsl(0deg, 100%, 74%);
}

.input-wrapp label {
  --mywidth: 100%;
  position: absolute;
  top: 30%;
  left: 5%;
  font-weight: 600;
  font-size: 0.9375rem;
  color: hsl(var(--dark-blue));
  border: 0;
}

.input-error {
  position: absolute;
  right: -20px;
  color: hsl(var(--red));
}
.input-error::before {
  position: relative;
  top: -40px;
  right: 40px;
  float: right;
  content: url("/images/icon-error.svg");
}

.input-btn {
  justify-content: center;
  background-color: hsl(var(--green));
  border-radius: 0.5rem;
  padding: 1.25rem;
  color: hsl(var(--white));
  text-transform: uppercase;
  font-weight: 600;
}
.input-btn:hover, .input-btn:focus {
  background-color: hsl(var(--green)/0.8);
}

small {
  color: hsl(var(--gray-blue));
  font-weight: 600;
  font-size: 0.75rem;
  padding: 0 1.875rem;
}